div * {
  padding: 0;
  margin: 0;
}
.App {
  display: flex;
  flex-direction: column;
  gap: 60px;
}
section {
  padding-top: 120px;
  width: calc(100% - 280px);
  margin: auto;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  font-size: 50px;
  color: var(--white);
  line-height: 60px;
}

::selection {
  background: var(--azure-button);
}
::placeholder {
  color: rgb(130, 130, 130);
  font-size: 12;
}
