.list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.list__string {
  list-style: none;
  font-size: 20px;
}
.list__string_elem {
  color: var(--white);
  text-decoration: none;
  display: flex;
  align-items: center;
}
.list__string_elem:hover {
  color: var(--red);
}
.elem_img {
  width: 50px;
}
