@mixin buttonGreen() {
    background-color: var(--azure-button);
    border: none;
    color: var(--white);
    cursor: pointer;
    font-size: 14px;
    width: 100px;
    height: 50px;
    margin-left: auto;
    margin-top: 5px;
    &:hover {
        background-color: var(--red);
    }
}